.tabs {
    font-family: arial;
    margin: 10px auto 0;
    width: 80%;
    text-align: left;
}
.tab-buttons {
    margin: 0 auto;
    position: flex;
    left: 50%;
    right: 50%;
    text-align: center;

}
.about-text-1 {
    margin-top: 25px;
