}

.about-text-1-bck {
    margin-top: 25px;
    font-size: 20px;
    background: linear-gradient(-315deg, #181964, #0045a0);
    padding: 10px 1.5rem;
    color: white;
}

.about-text-2 {
    font-size: 20px;
