button {
    margin-right: 1px;
    font-size: 20px;
    background: transparent;
    border: none;
    outline: none;
    padding: 10px 20px;
    cursor: pointer;
    color: #333;
    transition: all ease-in-out 0.2s;
    border-bottom: 2px solid transparent;
}
.bttn {
    margin-right: 1px;
    font-size: 20px;
    background: transparent;
    border: none;
    outline: none;
    padding: 10px 20px;
    cursor: pointer;
    color: rgba(51, 51, 51, 0.4);
    transition: all ease-in-out 0.2s;
    border-bottom: 2px solid transparent;
}

.active {
    border-bottom: 1px solid #42b3f4;
    color: black;
}
